/* ---- Form ---- */
.form-radio input[type="radio"] {
  display: none;
}
.form-radio input[type="radio"] + label {
  font-weight: 500;
}
.form-radio input[type="radio"] + label .icon {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 3px 5px 0 0;
  vertical-align: top;
  cursor: pointer;
  -moz-border-radius: 50%;
  border-radius: 50%;
  background-color: #FFF;
  border: 1px solid #e7e8e8;
  position: relative;
  -webkit-transition: background-color 0.4s linear;
  -o-transition: background-color 0.4s linear;
  -moz-transition: background-color 0.4s linear;
  transition: background-color 0.4s linear;
}
.form-radio input[type="radio"] + label .icon:before {
  transition: 0.3s;
  content: '';
  display: block;
  width: 7px;
  height: 7px;
  background-color: #2d2d2d;
  border-radius: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 3px;
}
.form-radio input[type="radio"] + label .label-align {
  display: inline-block;
  width: 94%;
}
.form-radio input[type="radio"]:checked + label .icon:before {
  opacity: 1;
}
.form-checkbox input[type="checkbox"] {
  display: none;
}
.form-checkbox input[type="checkbox"] + label {
  font-weight: 500;
}
.form-checkbox input[type="checkbox"] + label .icon {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin: 3px 5px 0 0;
  vertical-align: top;
  cursor: pointer;
  background-color: #FFF;
  border: 1px solid #e7e8e8;
  position: relative;
  -webkit-transition: background-color 0.4s linear;
  -o-transition: background-color 0.4s linear;
  -moz-transition: background-color 0.4s linear;
  transition: background-color 0.4s linear;
}
.form-checkbox input[type="checkbox"] + label .icon:before {
  transition: 0.3s;
  font: normal normal normal 14px/1 FontAwesome;
  opacity: 0;
  content: "\f00c";
  display: block;
  color: #2d2d2d;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.form-checkbox input[type="checkbox"] + label .label-align {
  display: inline-block;
